import React from "react";
import "./styles.css";
//import card1
import Card1 from "./components/Card1";
import Card2 from "./components/Card2";
import cardsArr from "./data";

export default function App() {
  console.log(cardsArr);

  const cards = cardsArr.map((ele, index) => {
    return (
      <Card1
        key={index}
        img={ele.img}
        title={ele.title}
        text={ele.text}
        url={ele.url}
      />
    );
  });

  console.log(cards);
  return (
    <div className="App">
      <h1>Portugal: Land of good wine, beautiful nature and amazing people!</h1>
      <section className="cards">
        {cards}
        {/* <Card1 />
        <Card2 /> */}
      </section>
    </div>
  );
}
